.animate {
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.animate-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  align-items: center;
}
.animate .element {
  position: relative;
  width: 10px;
  height: 10px;
  background-color: #e9f5f9;
  justify-self: center;
  border-radius: 50%;
}